import React, {ChangeEvent, useCallback} from 'react'
import {DataSetName} from "../../../consts/DataSetName";
import {useDispatch, useSelector} from "react-redux";
import {setDatasetAction} from "../../../store/data/actions";
import {StoreState} from "../../../store";
import {DataState} from "../../../store/data/types";

export const DataSetPicker = () => {
  const { dataset } = useSelector<StoreState, DataState>(s => s.data)
  const dispatch = useDispatch()

  const handleDataSetChange = useCallback(({ target: { value } }: ChangeEvent<HTMLSelectElement>) => {
    if (value) {
      // @ts-ignore
      dispatch(setDatasetAction(DataSetName[value ] as unknown as DataSetName))
    } else {
      dispatch(setDatasetAction(null))
    }
  }, [dispatch])
  return (
    <div className={'data__container--picker'}>
      <label htmlFor={'dataset'}>Choose Dataset</label>
      <select  value={dataset || ''} id={'dataset'} onChange={handleDataSetChange}>
        {!dataset ? <option value={''} /> :  <option key={dataset} value={dataset}>{dataset}</option>}
        {Object.keys(DataSetName).map(key => <option key={key} value={key}>{key}</option>)}
      </select>
    </div>
  )
}
