import React from 'react'
import {useSelector} from "react-redux";
import {StoreState} from "../../../store";
import {DataState, KPIData} from "../../../store/data/types";

export const Kpi = () => {
   const { data } = useSelector<StoreState, DataState>(s => s.data)

  const dataset = data as KPIData[]

  return (
    <table>
       <thead>
       <tr>{Object.keys(dataset[0]).map(k => <th key={k}>{k}</th>)}</tr>
       </thead>
      <tbody>
      {
        dataset.map(k => <tr key={k.title}><td>{k.title}</td><td>{k.value}</td></tr>)
      }
      </tbody>
    </table>
  )
}
