import React, { useMemo } from 'react'
import {useSelector} from "react-redux";
import {Line} from 'react-chartjs-2';

import {StoreState} from "../../../store";
import {DataState, ChartData} from "../../../store/data/types";

export const Chart = () => {
  const { data } = useSelector<StoreState, DataState>(s => s.data)

  const dataset = useMemo(() => {
    const storeData = data as ChartData[]
    const getColor = (i: number) => `${47 * (i + 1)}, ${83 * (i + 1)}, ${35 * (i + 1)}`
    return {
      labels: storeData[0].labels,
      datasets: storeData.map((d, i) => ({
        label: d.title,
        fill: false,
        lineTension: 0.1,
        backgroundColor: `rgba(${getColor(i)},0.4)`,
        borderColor: `rgba(${getColor(i)},1)`,
        borderCapStyle: 'butt',
        borderDash: [],
        borderDashOffset: 0.0,
        borderJoinStyle: 'miter',
        pointBorderColor: `rgba(${getColor(i)}, 1)`,
        pointBackgroundColor: '#fff',
        pointBorderWidth: 1,
        pointHoverRadius: 5,
        pointHoverBackgroundColor: `rgba(${getColor(i)}, 1)` ,
        pointHoverBorderColor: 'rgba(220,220,220,1)',
        pointHoverBorderWidth: 2,
        pointRadius: 1,
        pointHitRadius: 10,
        data: d.dataset.data
      }))
    }
  }, [data])


  return (
    <>
    <Line data={{ labels: dataset.labels, datasets: dataset.datasets.slice(0, 2) }} />
    <Line data={{ labels: dataset.labels, datasets: dataset.datasets.slice(2, 3) }} />
    </>
  )
}
