import React, {FC} from 'react'

import './data.scss'
import {useSelector} from "react-redux";
import {StoreState} from "../../store";
import {DataState} from "../../store/data/types";
import {DataPresenter} from "./components/DataPresenter";
import {DataSetPicker} from "./components/DataSetPicker";

const Data: FC = () => {
   const { error, loading} = useSelector<StoreState, DataState>(s => s.data)

  return (
    <div className={'data'}>
      <div className={'data__container'}>
        <h1>Data Component</h1>
        {error && <span className={'data__container--error'}>{error}</span> }
        {loading && <div className={'data__container--loader'}>Please Wait...</div> }
        <DataSetPicker />
        <hr/>
        <DataPresenter />
      </div>
    </div>
  )
}

export default Data
