import React, {useMemo} from 'react';
import {useSelector} from "react-redux";
import {StoreState} from "../../../store";
import {DataState} from "../../../store/data/types";
import {DataSetName} from "../../../consts/DataSetName";
import {Kpi} from "./Kpi";
import {Chart} from "./Chart";
import Quality from "./Quality";
import {Pies} from "./Pies";

export const DataPresenter = () => {
  const { dataset, loading, error } = useSelector<StoreState, DataState>(s => s.data)

  const Component = useMemo(() => {
    switch (dataset) {
      case DataSetName.Charts:
        return Chart
      case DataSetName.Qualities:
        return Quality
      case DataSetName.KPIS:
        return Kpi
      case DataSetName.Pies:
        return Pies
      default:
        return () => <></>
    }
  }, [dataset])

  if (loading || error) {
    return  <></>
  }
  return <Component />
}
