import React, {useEffect, useMemo, useRef, useState} from 'react'
import {Doughnut} from 'react-chartjs-2';


export const Pies = () => {
  const [selectedIndex, setSelectedIndex] = useState(3)

  const borders = useMemo(() => {
    if (selectedIndex === 0) {
      return [4, 20, 20]
    } else if (selectedIndex === 1) {
      return [20, 4, 20]
    } else if (selectedIndex === 2) {
      return [20, 20, 4]
    } else {
      return [10, 10, 10]
    }
  }, [selectedIndex])

  const colors = useMemo(() => {
    if (selectedIndex === 0) {
      return ['rgba(107, 108, 139, 1)', 'rgba(36, 37, 46, 0.6)',  'rgba(254, 215, 1, 0.6)']
    } else if (selectedIndex === 1) {
      return ['rgba(107, 108, 139, 0.6)', 'rgba(36, 37, 46, 1)',  'rgba(254, 215, 1, 0.6)']
    } else  if (selectedIndex === 2) {
      return ['rgba(107, 108, 139, 0.6)', 'rgba(36, 37, 46, 0.6)',  'rgba(254, 215, 1, 1)']
    } else {
      return ['rgba(107, 108, 139, 1)', 'rgba(36, 37, 46, 1)',  'rgba(254, 215, 1, 1)']
    }
  }, [selectedIndex])

  const options = {
    cutoutPercentage: 50,
    responsive: true,
    legend: {
      position: 'left',
      labels: {
        usePointStyle: true,
        fontSize: 20,
        padding: 20
      }
    },
  }

  const data = {
    datasets: [{
      data: [50, 200, 100],
      backgroundColor: colors,
      hoverBorderColor: [
        'rgba(0,0,0, 0)',
        'rgba(0,0,0, 0)',
        'rgba(0,0,0, 0)'
      ],
      hoverBackgroundColor: [
        '#6b6c8b',
        '#24252e',
        '#fed701'
      ],
      borderWidth: borders,
      hoverBorderWidth: [0, 0 , 0]
    }],
    labels: [
      'Mobile',
      'Tablet',
      'Desktop'
    ]
  };

  return (
    <>
      <div className={'data__container--picker'} style={{ position: "absolute", right: '20px', bottom: '5vh' }}>
        <label>Select Device</label>
        <select value={selectedIndex} onChange={({ target: { value }}) => setSelectedIndex(+value)}>
          {
            [
              'Mobile',
              'Tablet',
              'Desktop',
              'All'
            ].map((v, i) => <option value={i} key={i}>{v}</option>)
          }
        </select>
      </div>
      <Doughnut data={data} options={options}  />
      </>
  )
}
